@keyframes slideUp {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

nav {
  background: #041e42;
  padding: 25px 50px 20px;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10001;
  @media all and (max-width: 576px) {
    padding: 25px 0;
  }
  div.row {
    @media all and (max-width: 576px) {
      margin: 0 !important;
    }
  }
  .nav-logo {
    .LogoImg {
      height: 50px;
      width: auto;
    }
  }
  .nav-menu-icon {
    display: none;
    cursor: pointer;
    transition: 0.3s;
    @media all and (max-width: 1080px) {
      display: flex;
    }
  }
  .nav-menu-list {
    display: flex;
    flex-direction: column;
    animation: slideUp 0.3s ease-in-out alternate forwards;
    @media all and (max-width: 1080px) {
      display: none;
      flex: 0 0 100%;
      align-items: flex-start;
      flex-direction: column;
    }
    &.opened {
      @media all and (max-width: 1080px) {
        display: flex;
      }
    }
    ul.sub-links,
    ul.main-links {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      @media all and (max-width: 1080px) {
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
      }
      li {
        display: flex;
        &:not(:last-of-type) {
          margin-right: 20px;
        }
        a {
          text-transform: uppercase;
          line-height: 1.5;
          font-weight: 400;
          @media all and (max-width: 1080px) {
            font-size: 0.875rem !important;
            line-height: 1.5 !important;
            padding: 5px 0 !important;
          }
        }
      }
    }
    ul.sub-links {
      margin-bottom: 10px;
      @media all and (max-width: 1080px) {
        order: 2;
        padding-top: 20px;
      }
      li {
        a {
          font-size: 0.75rem;
          color: #cba052;
          @media all and (max-width: 1080px) {
            color: #ffffff;
          }
          &:hover {
            text-decoration: underline;
            @media all and (max-width: 1080px) {
              color: #f65275;
              text-decoration: none;
            }
          }
        }
      }
    }
    ul.main-links {
      @media all and (max-width: 1080px) {
        order: 1;
        border-bottom: 1px solid #fff;
        padding: 40px 0 20px;
      }
      .dropdown-menu-list-item {
        position: relative;
        @media all and (max-width: 1080px) {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
        }
        &:hover::after {
          display: none;
        }
        label {
          font-size: 0.875rem;
          font-weight: 600;
          color: #ffffff;
          display: flex;
          line-height: 1.5;
          text-transform: uppercase;
          cursor: pointer;
          padding: 5px 0;
          &:hover {
            color: #f65275;
          }
        }
        input {
          display: none;
        }
        input[type="checkbox"]:checked ~ ul.dropdown-menu-list {
          animation: slideUp 0.3s ease-in-out alternate forwards;
          @media all and (max-width: 1080px) {
            display: flex;
            position: relative;
            display: flex;
            align-items: flex-start;
            padding-left: 20px;
            flex-direction: column;
          }
          &::after {
            position: absolute;
            top: -5px;
            left: 0;
            right: 0;
            width: 100%;
            height: 5px;
            background: #f65275;
            content: "";
            @media all and (max-width: 1080px) {
              display: none;
            }
          }
        }
        a {
          display: flex;
          align-items: center;
        }
        ul.dropdown-menu-list {
          visibility: hidden;
          opacity: 0;
          position: absolute;
          top: calc(100% + 20px);
          left: 0;
          right: 0;
          width: 100%;
          display: flex;
          z-index: 2;
          background: #041e42;
          flex-direction: column;
          align-items: center;
          li {
            margin: 0;
            padding: 0;
            a {
              padding: 0;
              margin: 0;
              line-height: 40px;
              &:hover {
                color: #f65275;
              }
            }
            &::after {
              animation: none;
              visibility: hidden;
              opacity: 0;
              display: none;
            }
          }
        }
      }
      li {
        position: relative;
        a {
          font-size: 0.875rem;
          font-weight: 600;
          color: #ffffff;
          padding: 5px 0;
          &:hover {
            @media all and (max-width: 1080px) {
              color: #f65275;
            }
          }
        }
        &::after {
          display: none;
          content: "";
          bottom: -20px;
          height: 5px;
          width: 100%;
          left: 0;
          right: 0;
          background: #f65275;
          position: absolute;
          animation: slideUp 0.3s ease-in-out alternate forwards;
        }
        &:hover::after {
          display: block;
          @media all and (max-width: 1080px) {
            display: none;
          }
        }
      }
    }
  }
}
