.lift {
  background-image: url("https://ake.com.tr/uploads/images/Slider10.jpg");
  background-position: 50% -1.5 px;
  background-attachment: fixed;
  padding: 0;
  margin: 0;
  height: 400px;
  background-size: cover;
  background-repeat: no-repeat;
  justify-content: center;
  padding: 80px 0 0 110px;
}
.lift__contact {
  position: relative;
  border: 10px solid white;
  width: 200px;
  padding: 80px 0px 80px 30px;
  border-right: 0;
  .lift-item {
    h2 {
      font-size: 18px;
      letter-spacing: 5px;
      font-weight: 300;
      width: 500px;
    }
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 22%;
    width: 10px;
    background-color: white;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    height: 22%;
    width: 10px;
    background-color: white;
  }
}
.content1 {
  ul {
    display: flex;
    align-items: center;
    color: #fff;
    font-size: 13px;
    font-weight: 400;
    .link {
      color: #fff;
      transition: 0.5s;
    }
    .link:hover {
      color: rgb(31, 30, 30);
    }
    li {
      color: #fff;
    }
  }
  .list {
    color: #fff;
  }
}
.desk {
  position: relative;
  overflow: hidden;
  z-index: 0;
  &::before {
    width: 790px;
    height: 930px;
    position: absolute;
    content: "";
    border: 40px solid rgba(191, 191, 191, 0.1) !important;
    transform: rotate(45deg);
    transform: rotate(45deg);
    transform: rotate(45deg);
    transform: rotate(45deg);
    transform: rotate(45deg);
    right: -700px;
    bottom: -340px;
  }
}
.section {
  background: #f6f7f8;
  padding: 90px 190px 50px 70px;

  .container {
    padding: 20px;
  }
  .Section-full {
    h4 {
      color: #4e5f70;
      font-family: "Poppins", sans-serif;
      font-weight: 600;
      line-height: 30px;
      font-size: 1.3rem;
    }
    .section-title {
      padding-top: 22px;
      p {
        color: #4e5f70;

        font-size: 1.3rem;
        line-height: 24px;
        font-weight: 700;
      }
    }
    .text {
      color: #4e5f70;
      font-family: "Poppins", sans-serif;
      font-weight: 600;
      line-height: 30px;
      font-size: 14px;
    }
    .section-tim {
      padding-top: 40px;
    }
    h2 {
      font-size: 40px;
      font-weight: 600;
      line-height: 46px;
      color: #000;
      font-family: "Poppins", sans-serif;
      padding-right: 50px;
    }
  }
}

.fotter {
  background: #f6f7f8;
  padding: 50px 0;
}
.container1 {
  text-align: center;
}
.row {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 0 40px;
  .row1 {
    padding-right: 100px;
  }
}

