@media screen and (max-width: 792px) {
  .lift {
    padding: 46px 0 0 30px;
  }
}
@media screen and (max-width: 792px) {
  .section {
    padding: 25px 0px;
  }
}
@media screen and (max-width: 550px) {
  .fotter {
    padding: 10px 10px;
  }
}
