/*showcase start*/

.CareersShowcase {
  height: 600px;
  color: #fff;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 600px;
    width: 100%;
    background-image: url("./images/careers-header.jpg");
    filter: brightness(60%);
    z-index: -1;
    background-size: cover;
  }
  .text-wrapper {
    animation-name: toUp;
    animation-iteration-count: 1;
    animation-duration: 1.5s;
    h1 {
      font-weight: 700;
      font-size: 3.5rem;
    }
    .showcase-btn {
      margin-top: 30px;
      padding: 10px 25px;
      border: 2px solid #fff;
      background-color: #f65275;
      transition: 0.5s;
      color: #fff;
      font-weight: 600;
      &:hover {
        background-color: transparent;
      }
      &:first-child {
        margin-right: 10px;
      }
    }
  }
  &::after {
    position: absolute;
    content: "";
    height: 20vh;
    width: 0.5px;
    background-color: #b4b4b4;
    bottom: 0;
    left: 50%;
    animation-name: toUpSign;
    animation-iteration-count: 1;
    animation-duration: 4s;

    transform: translate(-50%, 50%);
  }
}
@keyframes toUp {
  0% {
    transform: translateY(300px);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes toUpSign {
  0% {
    transform: translate(-50%, 100%);
    opacity: 0.1;
    background-color: transparent;
  }
  50% {
    background-color: #b4b4b4;
  }
  100% {
    transform: translate(-50%, 50%);
    opacity: 1;
  }
}

/*showcase end*/

/*textpage start*/
.title {
  font-weight: 600;
  letter-spacing: 1.5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 5px;
    width: 50px;
    background-color: #cba052;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

/*textpage start*/

.CareersTextPage {
  background-color: #fff;
  text-align: center;
  padding: 100px 0;
  .wrapper {
    width: 60%;
    margin: 0 auto;
    p {
      font-size: 1.3rem;
    }
  }
}

/*textpage end*/

/*Apartment start*/
.Apartment {
  text-align: center;
  .article {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 80%;
    margin: 100px auto;
    .icon-wrapper {
      position: relative;
      display: inline-block;
      height: 50px;
      width: 50px;
      img {
        height: 30px;
        width: 30px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &::after {
        content: "";
        position: absolute;
        display: inline-block;
        height: 50px;
        width: 50px;
        border: 2px solid #cba052;
        top: 0;
        left: 0;
        transform: rotate(45deg);
      }
    }

    p {
      margin: 0;
      font-size: 1.3rem;
    }
    .line-icon {
      display: inline-block;
      width: 20px;
      height: 1px;
      background-color: #000;
    }
  }
}
/*Apartment end*/

/*Galleries end*/
.careers-galleries {
  padding: 100px 0;
  .gallery-1 {
    height: 540px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 1350px;
    margin: 0 auto;

    .gallery-item-text {
      position: relative;
      .text {
        position: absolute;
        border: 1px solid #cba052;
        top: 50%;
        transform: translateY(-50%);
        left: 100px;
        width: 650px;
        height: 82%;
        padding: 50px 150px 50px 50px;
      }
    }

    .gallery-item-img-1 {
      background-image: url(./images/otis_careers_engineering_image.png);
    }
  }

  .gallery-2 {
    height: 540px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 1350px;
    margin: 20px auto;

    .gallery-item-text {
      position: relative;
      .text {
        position: absolute;
        border: 1px solid #cba052;
        top: 50%;
        transform: translateY(-50%);
        right: 100px;
        width: 650px;
        height: 81%;
        padding: 50px 50px 50px 150px;
      }
    }

    .gallery-item-img-2 {
      background-image: url(./images/otis_careers_elevator_service_image.png);
    }
  }

  .gallery-item-img-3 {
    background-image: url(./images/otis_careers_elevator_manufacturing_image.png);
  }
  .gallery-item-img-4 {
    background-image: url(./images/otis_sales_careers_image2.png);
  }
  .gallery-item-img-5 {
    background-image: url(./images/careers-functional.png);
  }
}

/*Galleries end*/
/*Join start*/

.careersJoin {
  width: 100%;
  height: auto;
  background-image: url("./images/woman-walking-up-escalator-careers-new.jpg");
  text-align: center;
  padding: 150px 0;
  color: #fff;
  background-repeat: no-repeat;
  background-size: cover;
  .join-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    h1 {
      margin: 50px 0;
    }
  }
}

/*Join end*/

@media (max-width: 1260px) {
  .careers-galleries {
    .gallery-1 {
      grid-template-columns: repeat(1, 1fr);
      width: 90%;
      height: auto;
      .gallery-item-text {
        height: auto;
        position: static;
        order: 2;
        .text {
          position: static;
          width: 90%;
          height: auto;
          padding: 0;
          transform: translateY(-30px);
          padding: 50px;
          margin: 0 auto;
        }
      }
      .gallery-item-img {
        height: 400px;
        order: 1;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
    .gallery-2 {
      margin-top: 100px;
      grid-template-columns: repeat(1, 1fr);
      width: 90%;
      height: auto;

      .gallery-item-text {
        height: auto;
        position: static;
        order: 2;
        .text {
          position: static;
          width: 90%;
          height: auto;
          padding: 50px;
          transform: translateY(-30px);
          margin: 0 auto;
        }
      }
      .gallery-item-img {
        height: 400px;
        order: 1;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    .gallery-item-img-1 {
      height: 400px;
    }
    .gallery-item-img-2 {
      height: 400px;
    }
    .gallery-item-img-3 {
      height: 400px;
    }
    .gallery-item-img-4 {
      height: 400px;
    }
    .gallery-item-img-5 {
      height: 400px;
    }
  }
}

@media (max-width: 1000px) {
  .Apartment .article {
    row-gap: 30px;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .careers-galleries {
    .gallery-item-img-1 {
      background-position-x: right;
    }
    .gallery-item-img-2 {
      background-position-x: right;
    }
    .gallery-item-img-3 {
      background-position-x: center;
    }
    .gallery-item-img-4 {
      background-position-x: right;
    }
    .gallery-item-img-5 {
      background-position-x: right;
    }
  }
  .CareersShowcase .text-wrapper h1 {
    font-weight: 600;
    font-size: 2rem;
  }
  .Apartment .article {
    grid-template-columns: repeat(1, 1fr);
  }
  .CareersTextPage {
    .wrapper {
      margin: 0 auto;
      width: 90%;
      p {
        text-align: left;
      }
    }
  }
}
