/*showcase start*/

.showcase {
  height: 600px;
  color: #fff;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 600px;
    width: 100%;
    background-image: url("./images/showcase-mw-1200.jpg");
    filter: brightness(60%);
    z-index: -1;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .text-wrapper {
    animation-name: toUp;
    animation-iteration-count: 1;
    animation-duration: 1.5s;
    h1 {
      font-weight: 700;
      font-size: 3.5rem;
    }
    .showcase-btn {
      margin-top: 30px;
      padding: 10px 25px;
      border: 2px solid #fff;
      background-color: #f65275;
      transition: 0.5s;
      color: #fff;
      font-weight: 600;
      &:hover {
        background-color: transparent;
      }
    }
  }
  &::after {
    position: absolute;
    content: "";
    height: 30vh;
    width: 0.5px;
    background-color: #b4b4b4;
    bottom: 0;
    left: 50%;
    animation-name: toUpSign;
    animation-iteration-count: 1;
    animation-duration: 4s;

    transform: translate(-50%, 50%);
  }
}
@keyframes toUp {
  0% {
    transform: translateY(300px);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes toUpSign {
  0% {
    transform: translate(-50%, 100%);
    opacity: 0.1;
    background-color: transparent;
  }
  50% {
    background-color: #b4b4b4;
  }
  100% {
    transform: translate(-50%, 50%);
    opacity: 1;
  }
}

/*showcase end*/
/*textpage start*/
.title {
  font-weight: 600;
  letter-spacing: 1.5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 5px;
    width: 50px;
    background-color: #cba052;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.TextPage {
  background-color: #f8f7f7;
  text-align: center;
  padding: 100px 0;
  .wrapper {
    width: 60%;
    margin: 0 auto;
    p {
      font-size: 1.3rem;
    }
  }
}

/*textpage end*/

/*cards start*/
.cards {
  background-color: #f8f7f7;
  padding: 100px 0;
  img {
    width: 100%;
    max-height: 100%;
    transition: transform 0.9s;
  }
  .cards-wrapper {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;

    .card-wrapper {
      background-color: #fff;

      .card-img-wrapper {
        overflow: hidden;
      }
      h4 {
        margin-top: 20px;
        padding: 0 10px;
        color: #041e5f;
        font-weight: 800;
      }
      p {
        margin: 0;
        background-color: #f65275;
        color: #fff;
        padding: 0 10px;
      }
      .button-link {
        display: block;
        text-align: center;
        .button-dark {
          text-decoration: none;
          display: inline-block;
          width: 80%;
          padding: 8px 0;
          background-color: #041e42;
          border: 2px solid #041e42;
          color: #fff;
          transition: 0.3s ease;
          margin: 30px 0;
        }
        .button-dark:hover {
          background-color: transparent;
          color: #041e42;
        }
      }
      &:hover img {
        transform: scale(1.1);
      }
    }
  }
}

/*cards end*/

/*gallery start*/
.button-dark {
  text-decoration: none;
  display: inline-block;
  width: 80%;
  padding: 8px 0;
  background-color: #041e42;
  border: 2px solid #041e42;
  color: #fff;
  transition: 0.3s ease;
  margin: 30px 0;
}
.button-dark:hover {
  background-color: transparent;
  color: #041e42;
}
.modernization-galleries {
  padding: 100px 0;
  .gallery-1 {
    height: 540px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 1350px;
    margin: 0 auto;

    .gallery-item-text {
      position: relative;
      .text {
        position: absolute;
        border: 1px solid #cba052;
        top: 50%;
        transform: translateY(-50%);
        left: 100px;
        width: 650px;
        height: 82%;
        padding: 50px 150px 50px 50px;
      }
    }

    .gallery-item-img {
      background-image: url(./images/performance_mod_boxes.jpg);
    }
  }

  .gallery-2 {
    height: 540px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 1350px;
    margin: 20px auto;

    .gallery-item-text {
      position: relative;
      .text {
        position: absolute;
        border: 1px solid #cba052;
        top: 50%;
        transform: translateY(-50%);
        right: 100px;
        width: 650px;
        height: 81%;
        padding: 50px 50px 50px 150px;
      }
    }

    .gallery-item-img {
      background-image: url(./images/upgrades_page_boxes.jpg);
    }
  }
}

/*gallery end*/

/*cardJump start*/
.cardJump-wrapper {
  img {
    max-width: 100%;
    max-height: 100%;
  }
  width: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  margin: 100px auto;
  .cardJump {
    .cardJump-inside {
      cursor: pointer;
      &:hover img {
        transform: translate(2%, -2%);
      }
      h3 {
        margin: 20px 0;
        color: #000;
      }
      .card-img-wrapper {
        background-color: #f65275;
        img {
          transition: 0.3s;
        }
      }
    }
    .line {
      margin-top: 50px;
      width: 100%;
      height: 4px;
      background-color: #041e5f;
    }
  }
}
/*cardJump end*/

@media (max-width: 1260px) {
  .modernization-galleries .gallery-1 {
    grid-template-columns: repeat(1, 1fr);
    width: 90%;
    height: auto;
    .gallery-item-text {
      height: auto;
      position: static;
      order: 2;
      .text {
        position: static;
        width: 90%;
        height: auto;
        padding: 0;
        transform: translateY(-30px);
        padding: 50px;
        margin: 0 auto;
      }
    }
    .gallery-item-img {
      height: 400px;
      order: 1;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  .modernization-galleries .gallery-2 {
    margin-top: 100px;
    grid-template-columns: repeat(1, 1fr);
    width: 90%;
    height: auto;

    .gallery-item-text {
      height: auto;
      position: static;
      order: 2;
      .text {
        position: static;
        width: 90%;
        height: auto;
        padding: 50px;
        transform: translateY(-30px);
        margin: 0 auto;
      }
    }
    .gallery-item-img {
      height: 400px;
      order: 1;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
}

@media (max-width: 1100px) {
  .cards .cards-wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
  .cardJump-wrapper {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 700px) {
  .cards .cards-wrapper {
    grid-template-columns: repeat(1, 1fr);
  }
  .button-dark {
    font-size: 1rem;
  }
}
