section#service-maintenance {
  .header-wrapper {
    background-color: #041e42;
    header {
      max-width: 1322px;
      margin-right: auto;
      margin-left: auto;
      color: #ffffff;
      position: relative;
      height: 500px;
      display: flex;
      align-items: center;
      img.header-background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 400px;
        height: 100%;
      }

      img.service-logos {
        height: 182px;
        z-index: 2;
        @media all and (max-width: 768px) {
          width: 100%;
          height: auto;
        }
      }
      h1 {
        font-size: 2.5rem;
        line-height: 40px;
        font-weight: 700;
        font-family: "Grifo M", sans-serif;
        margin-bottom: 64px;
        position: relative;
        justify-content: center;
        display: flex;
        text-align: left;
        &::after {
          width: 50px;
          height: 4px;
          background: #cba052;
          content: "";
          position: absolute;
          bottom: -20px;
        }
        @media all and (max-width: 768px) {
          text-align: center;
        }
      }
      p {
        font-size: 1.125rem;
        line-height: 1.5;
        margin-bottom: 56px;
        @media all and (max-width: 768px) {
          text-align: center;
        }
      }
      button {
        padding: 10px 30px;
        border: 2px solid #ffffff;
        background: transparent;
        text-transform: uppercase;
        color: #ffffff;
        line-height: 21px;
        font-size: 0.875rem;
        letter-spacing: 1px;
        &:hover {
          border: 2px solid #f65275;
          background: #f65275;
        }
      }
    }
  }
  main {
    article.needs {
      color: #ffffff;
      background: #0033a0;
      padding: 120px 0 75px;
      h2 {
        font-size: 3.125rem;
        font-family: Grifo M, sans-serif;
        font-weight: 400;
        line-height: 1;
        margin-bottom: 50px;
        text-align: center;
        display: flex;
        align-items: center;
        flex-direction: column;
        position: relative;
        &::after {
          background: #f65275;
          content: "";
          bottom: -16px;
          width: 50px;
          height: 4px;
          position: absolute;
        }
      }
      p {
        font-size: 1.3rem;
        text-align: center;
        color: #ffffff;
        line-height: 1.5;
        margin-bottom: 56px;
      }
      button {
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: 400;
        border: 2px solid transparent;
        color: #ffffff;
        padding: 10px 20px;
        background: #f65275;
        font-size: 0.875rem;
        &:hover {
          background: transparent;
          border: 2px solid #ffffff;
        }
      }
    }
    article.equipment {
      padding: 75px 0;
      margin: 0;
      .equipment-items-container {
        position: relative;
        padding: 60px 60px 60px 0;
        z-index: 2;
        @media all and (max-width: 992px) {
          margin-bottom: 60px;
          padding: 0;
        }
        &::before {
          content: "";
          display: block;
          position: absolute;
          top: 15px;
          left: -60px;
          right: -60px;
          bottom: 15px;
          border: 1px solid #cba052;
          transition: all 0.75s ease;
          @media all and (max-width: 992px) {
            display: none;
          }
        }
        h3 {
          color: #041e42;
          font-size: 1.875rem;
          font-family: "Grifo M", sans-serif;
          font-weight: 400;
          line-height: 1.2;
          margin: 0 0 30px;
          @media all and (max-width: 992px) {
            text-align: center;
          }
        }
        & > p:first-of-type {
          font-size: 1.125rem;
          color: #000;
          line-height: 1.5;
          font-weight: 400;
          margin-bottom: 1rem;
          @media all and (max-width: 992px) {
            text-align: center;
          }
        }
        & > p:last-of-type {
          font-size: 1.125rem;
          color: #000;
          line-height: 1.5;
          font-weight: 700;
          text-align: center;
          margin-bottom: 1rem;
        }
        &-wrap {
          display: grid;
          grid-template-columns: 1fr 1fr;
          grid-template-rows: 1fr 1fr;
          .equipment-item {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: flex-start;
            padding: 0;
            margin: 0;
            img {
              width: 100px;
              height: 100px;
            }
            h4 {
              margin-bottom: 1rem;
              font-size: 1.125rem;
              color: #000;
              text-align: center;
              line-height: 1.5;
              font-weight: 400;
            }
          }
          @media all and (max-width: 576px) {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr;
          }
        }
      }
      .equipment-image-container {
        position: relative;
        overflow: hidden;
        z-index: 1;
        height: 520px;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: auto;
          height: 100%;
        }
      }
    }
    .about-us-wrapper {
      background-color: #041e42;
      article.about-us {
        max-width: 1322px;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        height: 500px;
        width: 100%;
        overflow: hidden;
        color: #fff;
        justify-content: center;
        display: flex;
        flex-direction: column;
        @media all and (max-width: 768px) {
          height: 640px;
        }
        @media all and (max-width: 576px) {
          height: 720px;
        }
        img.about-us-background-image {
          // z-index: -1;
          position: absolute;
          width: 100%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          height: 100%;
        }
        p:first-of-type {
          line-height: 1;
          font-weight: 600;
          font-size: 0.875rem;
          letter-spacing: 1px;
          text-transform: uppercase;
          display: flex;
          align-items: center;
          position: relative;
          flex-direction: column;
          margin-bottom: 40px;
          &::after {
            position: absolute;
            bottom: -16px;
            width: 50px;
            height: 4px;
            background: #cba052;
            content: "";
          }
        }
        h2 {
          text-align: center;
          font-size: 2.75rem;
          font-family: "Grifo M", sans-serif;
          line-height: 1;
          margin-bottom: 30px;
        }
        p:nth-of-type(2) {
          margin-bottom: 16px;
          font-weight: 400;
          font-size: 1.125rem;
        }
        p {
          text-align: center;
        }
      }
    }
    article.experience {
      padding: 75px 0;
      background: linear-gradient(180deg, #fff, #eceae8);
      h2 {
        margin-bottom: 64px;
        font-family: "Grifo M", sans-serif;
        font-size: 2.75rem;
        line-height: 1;
        color: #041e42;
        display: flex;
        align-items: center;
        flex-direction: column;
        position: relative;
        &::after {
          position: absolute;
          bottom: -16px;
          height: 4px;
          width: 50px;
          background: #cba052;
          content: "";
        }
        & ~ span {
          margin-bottom: 1rem;
        }
      }
      .experience-item {
        img {
          height: 81px;
          width: auto;
          margin-bottom: 1rem;
        }
        h3 {
          font-family: "Grifo M", sans-serif;
          font-size: 1.75rem;
          margin-bottom: 1rem;
          color: #041e42;
          font-weight: 500;
          line-height: 1.2;
        }
        p {
          font-size: 1rem;
          color: #041e42;
          line-height: 1.5;
          font-weight: 400;
          margin-bottom: 1rem;
          text-align: center;
          width: 80%;
        }
      }
      .maintenance-info-item {
        &:first-of-type {
          position: relative;
          padding: 60px 60px 60px 0;
          z-index: 2;
          @media all and (max-width: 992px) {
            padding: 0;
            text-align: center;
            margin-bottom: 60px;
          }
          &::before {
            content: "";
            display: block;
            position: absolute;
            top: 15px;
            left: -60px;
            right: -60px;
            bottom: 15px;
            border: 1px solid #cba052;
            transition: all 0.75s ease;
            @media all and (max-width: 992px) {
              display: none;
            }
          }
        }
        &:nth-of-type(2) {
          position: relative;
          height: 400px;
          overflow: hidden;
          z-index: 1;
          img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: auto;
            width: 100%;
          }
        }
        h3 {
          color: #041e42;
          font-size: 1.875rem;
          font-family: "Grifo M";
          font-weight: 400;
          line-height: 1.2;
        }
        p {
          &:first-of-type {
            font-weight: 300;
            font-size: 1.25rem;
            margin-bottom: 1rem;
            color: #000;
            line-height: 1.5;
          }
          &:nth-of-type(2) {
            margin-bottom: 1rem;
            font-size: 1.125rem;
            color: #000;
            line-height: 1.5;
            font-weight: 400;
          }
        }
      }
      .service-info-item {
        &-image-container {
          position: relative;
          width: 100%;
          height: 240px;
          overflow: hidden;
          &:hover {
            img {
              transform: translate(-50%, -50%) scale(1.1);
            }
          }
          img {
            min-height: 100%;
            width: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
        &-data-container {
          padding: 20px;
          background: #ffffff;
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          height: 420px;
          h3 {
            line-height: 1.2;
            font-weight: 500;
            margin-bottom: 1rem;
            color: #041e42;
            font-family: "Grifo M", sans-serif;
            font-size: 1.75rem;
          }
          h4 {
            color: #041e42;
            font-size: 1.5rem;
            font-family: "Grifo M", sans-serif;
            font-weight: 400;
            line-height: 1.2;
            margin-bottom: 1rem;
          }
          p {
            font-size: 1rem;
            color: #000;
            line-height: 1.5;
            margin-bottom: 1rem;
            font-weight: 400;
            height: auto;
          }
          a {
            text-align: center;
            display: block;
            width: 100%;
            margin-top: auto;
            padding: 10px 0;
            color: #fff;
            font-size: 0.875rem;
            font-weight: 600;
            text-transform: uppercase;
            background: #041e42;
            border: 2px solid transparent;
            &:hover {
              border: 2px solid #041e42;
              background: transparent;
              color: #041e42;
            }
          }
        }
      }
    }
    article.additional-information {
      padding: 150px 0;
      .d-flex.flex-row {
        .additional-info-data-container {
          padding: 60px 60px 60px 0;
          @media all and (max-width: 992px) {
            padding: 0;
          }
        }
      }
      .d-flex.flex-row-reverse {
        .additional-info-data-container {
          padding: 60px 0 60px 60px;
          @media all and (max-width: 992px) {
            padding: 0;
          }
        }
      }
      .additional-info-data-container {
        position: relative;
        z-index: 2;
        &::before {
          content: "";
          display: block;
          position: absolute;
          top: 15px;
          left: -60px;
          right: -60px;
          bottom: 15px;
          border: 1px solid #cba052;
          transition: all 0.75s ease;
          @media all and (max-width: 992px) {
            display: none;
          }
        }
        h2 {
          color: #041e42;
          font-size: 1.875rem;
          font-family: "Grifo M", serif;
          font-weight: 400;
          line-height: 1.2;
          margin-bottom: 1rem;
        }
        h3 {
          font-size: 1.25rem;
          font-weight: 300;
          margin-bottom: 1rem;
          color: #000;
          line-height: 1.5;
        }
        p {
          font-size: 1.125rem;
          color: #000;
          line-height: 1.5;
          margin-bottom: 56px;
          font-weight: 400;
        }
        a {
          color: #fff;
          border: 2px solid transparent;
          background: #041e42;
          font-size: 0.875rem;
          letter-spacing: 1px;
          padding: 10px 30px;
          font-size: 0.875rem;
          text-transform: uppercase;
          &:hover {
            color: #041e42;
            border: 2px solid #041e42;
            background: transparent;
          }
        }
      }
      .additional-info-image-container {
        position: relative;
        height: 540px;
        z-index: 1;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: auto;
        }
      }
    }
    article.additional-part {
      background: white;
      padding: 50px 0 100px;
      .additional-part-info {
        padding: 75px 0;
        h1 {
          color: #041e42;
          font-size: 3.125rem;
          font-family: "Grifo M", sans-serif;
          font-weight: 400;
          line-height: 1;
          margin-bottom: 64px;
          display: flex;
          align-items: center;
          position: relative;
          flex-direction: column;
          &::before {
            position: absolute;
            display: block;
            width: 50px;
            height: 4px;
            content: "";
            background: #cba052;
            bottom: -20px;
          }
        }
        p {
          color: #041e42;
          text-align: center;
          font-size: 20px;
          line-height: 1.5;
          margin-bottom: 56px;
        }
        a {
          text-transform: uppercase;
          letter-spacing: 1px;
          color: #ffffff;
          background: #041e42;
          padding: 10px 30px;
          border: 2px solid transparent;
          &:hover {
            background: transparent;
            border: 2px solid #041e42;
            color: #041e42;
          }
        }
      }
      .additional-part-links {
        .resourceImage {
          background: #f76786;
          position: relative;
          height: 280px;
          width: 100%;
          display: flex;
          img {
            width: 100%;
            min-height: 100%;
            align-items: center;
            justify-content: center;
          }
          &:hover {
            img {
              transform: translate(10px, -10px);
            }
          }
          @media (min-width: 768px) and (max-width: 992px) {
            height: 210px;
          }
        }
        a {
          display: block;
          padding: 25px 0 15px;
          border-bottom: 1px solid #041e42;
          font-family: "Griffo M";
          text-transform: capitalize;
          font-size: 1.875rem;
          color: #041e42;
        }
      }
    }
  }
}
