.blog {
  width: 360px;
  margin: 100px 25px 0px 25px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.blogImg {
  width: 360px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
  transition: 0.3s;
}
.blogImg:hover {
  cursor: pointer;
  transform: scale(1.1);
  opacity: 1;
}

.blogInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blogTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  cursor: pointer;
}

.blogDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #000000;
  margin-top: 20px;
  margin-right: 70%;
  width: 100px;
}

.blogDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}
@media (max-width: 900px) {
  .blog {
    margin-left: 20%;
  }
  .blogImg {
    width: 330px;
  }
}
@media (max-width: 580px) {
  .blogImg {
    width: 330px;
  }
}
@media (max-width: 500px) {
  .blog {
    margin-left: -20px;
  }
} ;
