.house-showcase {
  height: 600px;
  color: #fff;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 600px;
    width: 100%;
    background-image: url("./images/Slider2.jpg");
    filter: brightness(60%);
    z-index: -1;
    background-size: cover;
  }
  .text-wrapper {
    animation-name: toUp;
    animation-iteration-count: 1;
    animation-duration: 1.5s;
    h1 {
      font-weight: 700;
      font-size: 3.5rem;
    }
    .showcase-btn {
      margin-top: 30px;
      padding: 10px 25px;
      border: 2px solid #fff;
      background-color: #f65275;
      transition: 0.5s;
      color: #fff;
      font-weight: 600;
      &:hover {
        background-color: transparent;
      }
      &:first-child {
        margin-right: 10px;
      }
    }
  }
  &::after {
    position: absolute;
    content: "";
    height: 20vh;
    width: 0.5px;
    background-color: #b4b4b4;
    bottom: 0;
    left: 50%;
    animation-name: toUpSign;
    animation-iteration-count: 1;
    animation-duration: 4s;

    transform: translate(-50%, 50%);
  }
}
@keyframes toUp {
  0% {
    transform: translateY(300px);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes toUpSign {
  0% {
    transform: translate(-50%, 100%);
    opacity: 0.1;
    background-color: transparent;
  }
  50% {
    background-color: #b4b4b4;
  }
  100% {
    transform: translate(-50%, 50%);
    opacity: 1;
  }
}
.title {
  text-align: center;
  margin-top: 100px;
  font-weight: 600;
  letter-spacing: 1.5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 5px;
    width: 50px;
    background-color: #cba052;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.text {
  margin-top: 50px;
  font-size: 1.3rem;
  text-align: center;
}

.accordion-title {
  font-weight: 700;
  text-align: center;
}
.accordion {
  margin-bottom: 100px;

  #accordion-header {
    button {
      background-color: #eee !important;
    }
  }
  .accordion-body {
    font-size: 1.3rem;
  }
}
