@keyframes toUp {
  0% {
    transform: translateY(300px);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes toUpSign {
  0% {
    transform: translate(-50%, 100%);
    opacity: 0.1;
    background-color: transparent;
  }
  50% {
    background-color: #b4b4b4;
  }
  100% {
    transform: translate(-50%, 50%);
    opacity: 1;
  }
}
.title {
  text-align: center;
  margin-top: 100px;
  font-weight: 600;
  letter-spacing: 1.5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 5px;
    width: 50px;
    background-color: #cba052;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}
