section#usageAreas {
  background-color: #eceae8;
  .UsageAreasShowcase {
    height: 600px;
    color: #fff;
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      height: 600px;
      width: 100%;
      background-image: url("../usageAreasBackground.jpg");
      filter: brightness(60%);
      background-size: cover;
      background-position: right;
    }
    .text-wrapper {
      animation-name: toUp;
      animation-iteration-count: 1;
      animation-duration: 1.5s;
      h1 {
        font-weight: 700;
        font-size: 3.5rem;
      }
      .showcase-btn {
        margin-top: 30px;
        padding: 10px 25px;
        border: 2px solid #fff;
        background-color: #f65275;
        transition: 0.5s;
        color: #fff;
        font-weight: 600;
        &:hover {
          background-color: transparent;
        }
        &:first-child {
          margin-right: 10px;
        }
      }
    }
    &::after {
      position: absolute;
      content: "";
      height: 20vh;
      width: 0.5px;
      background-color: #b4b4b4;
      bottom: 0;
      left: 50%;
      animation-name: toUpSign;
      animation-iteration-count: 1;
      animation-duration: 4s;

      transform: translate(-50%, 50%);
    }
  }
  @keyframes toUp {
    0% {
      transform: translateY(300px);
      opacity: 0.1;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  @keyframes toUpSign {
    0% {
      transform: translate(-50%, 100%);
      opacity: 0.1;
      background-color: transparent;
    }
    50% {
      background-color: #b4b4b4;
    }
    100% {
      transform: translate(-50%, 50%);
      opacity: 1;
    }
  }

  .usage-wrapper {
    display: grid;
    grid-template-columns: 3fr 1fr;

    padding: 100px 0;
    .left {
      .left-inner {
        margin: 0 auto;
        width: 90%;
        .left-text {
          background-color: #041e42;
          color: #fff;
          padding: 50px 30px;
          border-radius: 20px;
          .title {
            margin-top: 0;
          }
        }
        .left-cards {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          background-color: #041e42;
          grid-gap: 30px;
          padding: 30px;
          border-radius: 20px;
          .card-wrapper {
            background-color: #fff;
            margin: 0;
            border-radius: 10px;
            & > p {
              display: none;
            }
            h4 {
              margin-top: 20px;
            }
            .card-img-wrapper {
              height: 300px;
              width: 100%;
              border-top-left-radius: 10px;
              border-top-right-radius: 10px;
              overflow: hidden;
              img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
    .right {
      height: fit-content;
      position: sticky;
      top: 150px;
      .usage-area-nav {
        padding: 30px;
        border-radius: 20px;
        background-color: #041e42;
        .wrapper {
          display: flex;
          flex-direction: column;
          a {
            padding: 5px;
            border-radius: 5px;
            color: #fff;
            transition: 0.3s;
            &:hover {
              color: #041e42;
              background-color: #fff;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 1065px) {
  section#usageAreas .usage-wrapper .right {
    top: 200px;
  }

  .title {
    font-size: 2.5rem !important;
  }
}
@media (max-width: 1000px) {
  section#usageAreas .usage-wrapper .left .left-inner .left-cards {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 992px) {
  section#usageAreas .usage-wrapper .right {
    top: 150px;
  }
}
@media (max-width: 900px) {
  section#usageAreas .usage-wrapper {
    grid-template-columns: 98% 2%;
    .left {
      margin-right: 20px;
    }
    .right {
      height: 366px;
      top: 120px;
      &:hover .usage-area-nav {
        padding: 30px;
        .wrapper {
          width: 300px;
          padding-left: 0px;
        }
      }
      .usage-area-nav {
        position: absolute;
        right: 0;
        padding-left: 0;
        padding-right: 0;
        height: 366px;
        transition: 1s;
        overflow: hidden;
        .wrapper {
          padding-left: 30px;
          width: 0;
          transition: 1s;
          a {
            width: 300px;
          }
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .title {
    font-size: 1.5rem !important;
  }
  .text-wrapper p {
    margin-top: 50px;
    font-size: 1rem !important;
  }
}
