* {
  margin: 0;
  padding: 0;
  font-family: grifo_mmedium, "Times New Roman", serif;
}
.aboutus-container {
  overflow: hidden;

  .aboutUsSection-1 {
    text-decoration: none;
    background-size: contain;
    background-repeat: no-repeat;
    align-items: center;
    position: relative;
    z-index: 1;
    .eskalator {
      width: 100%;
      height: 600px;
      filter: brightness(60%);
    }
    .aboutus-section-1-text {
      font-size: 64px;
      font-style: normal;
      font-weight: 700;
      line-height: 64px;
      position: absolute;
      top: 40%;
      left: 42%;

      align-items: center;
      color: #fff;
      display: grid;
      place-items: center;
      animation-name: InMoveTop;
      animation-duration: 1s;
    }

    @keyframes InMoveTop {
      0% {
        opacity: 0.1;
        transform: translateY(180px);
      }
      50% {
        opacity: 0.5;
      }

      100% {
        opacity: 1;
        transform: translate(0);
      }
    }

    .aboutus-section-1-stick {
      top: 83%;
      left: 50%;
      position: absolute;
      border-left: 1.5px solid rgb(209, 209, 209);
      height: 200px;
      animation-name: InMoveTop;
      animation-duration: 1s;
    }
  }
  .aboutus-section-2 {
    align-items: center;
    text-align: center;
    height: 400px;
    animation-name: InMoveTop;
    animation-duration: 1s;

    .aboutus-section-2-h1 {
      font-size: 50px;
      line-height: 50px;
      color: #041e42;
      font-weight: 400;
      font-style: normal;
      margin-top: 100px;
    }
    .aboutus-section-2-stick {
      position: absolute;
      border-top: 4px solid rgb(182, 119, 1);
      width: 55px;
      margin-top: 10px;
      height: 50px;
      left: 48%;
    }
    .aboutus-section-2-p {
      position: absolute;
      width: 50%;

      left: 25%;
      margin-top: 50px;
      font-size: 1.3rem;
    }
    .aboutus-section-2-button {
      margin-top: 230px;
    }
    .btn:link,
    .btn:visited {
      text-decoration: none;
      padding: 15px 40px;
      text-transform: uppercase;
      display: inline-block;
      border-radius: none;
      transition: all 0.3s;
      position: relative;
      background-color: #041e42;
      color: white;
      margin-left: 10px;
    }

    .btn:hover {
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
      background-color: #fff;
      color: #041e42;
      border: 3px solid #041e42;
    }
    .btn:active {
      transform: translateY(1px);
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    }
    .btn::after {
      content: "";
      display: inline-block;
      height: 100%;
      width: 100%;
      border-radius: 100px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      transition: all 0.3s;
    }
  }
  .aboutUsSection-2 {
    align-items: center;
    text-align: center;
    text-decoration: none;
    height: 527px;

    background-size: contain;
    color: #fff;
    background-repeat: no-repeat;
    filter: brightness(90%);
    position: relative;
    z-index: 1;

    .aboutus-section-2-text {
      width: 800px;
      font-size: 35px;
      font-style: normal;
      font-weight: 400;
      line-height: 50px;
      position: absolute;
      align-items: center;
      text-align: center;
      top: 40%;
      left: 20%;
      color: #fff;
      display: grid;
      animation-name: InMoveTop;
      animation-duration: 1s;
    }
    .aboutus-section-3-img {
      position: absolute;
      margin-left: 40%;
    }

    @keyframes InMoveTop {
      0% {
        opacity: 0.1;
        transform: translateY(180px);
      }
      50% {
        opacity: 0.5;
      }

      100% {
        opacity: 1;
        transform: translate(0);
      }
    }
    .aboutus-section-3 img {
      position: absolute;
      width: 100px;
      top: 100px;
    }
  }

  @media (max-width: 900px) {
    .aboutus-container {
      overflow: hidden;
      text-align: center;
      align-items: center;

      .aboutUsSection-1 img {
        object-fit: cover;
        height: 500px;
        align-items: center;
      }
      .aboutus-section-1-text {
        font-size: 32px;
        line-height: 32px;
        left: 10%;
        width: 80%;
      }
      .aboutus-section-1-stick {
        font-size: 18px;
        height: 120px;
        margin-top: 30px;
        line-height: 32px;
      }
      .aboutus-section-2 {
        flex-direction: column;
        align-items: center;
        text-align: center;
      }
      .aboutus-section-2-h1 {
        font-size: 30px;
      }

      .aboutus-section-2-p {
        font-size: 15px;

        width: 80%;
        right: center;
        text-align: center;
        left: 10%;
      }

      .btn-1 {
        font-size: 9px;
        width: 160px;
      }
      .btn-2 {
        font-size: 9px;
        width: 160px;
      }

      .aboutUsSection-2 img {
        object-fit: cover;
        height: 500px;
        align-items: center;
      }
      .aboutUsSection-2 h1 {
        font-size: 22px;
        line-height: 32px;
        width: 70%;
        left: 15%;
        right: center;
        text-align: center;
      }
    }
  }

  .aboutus-section-3 {
    img {
      max-width: 100%;
      max-height: 100%;
    }
    width: 90%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    margin: 100px auto;
    .Section_4-container {
      .Section_4-container-inside {
        cursor: pointer;
        &:hover img {
          transform: translate(2%, -2%);
        }
        h3 {
          margin: 20px 0;
          color: #000;
        }
        .card-img {
          background-color: #f65275;
          img {
            transition: 0.3s;
          }
        }
      }
      .Section_4-container-line {
        margin-top: 50px;
        width: 100%;
        height: 4px;
        background-color: #041e5f;
      }
    }
  }
  @media (max-width: 900px) {
    .aboutus-section-3 {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
