.register {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(rgba(8, 8, 8, 0.13), rgba(2, 2, 2, 0.205)),
    url("https://previews.123rf.com/images/mazirama/mazirama1502/mazirama150200268/36497171-registration-text-concept-on-green-digital-world-map-background.jpg");
  background-size: cover;
}

.registerTitle {
  color: white;
  font-size: 50px;
}

.registerForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.registerForm > label {
  color: #fff;
  margin: 10px 0;
}

.registerInput {
  padding: 10px;
  background-color: white;
  border: none;
  border-radius: 10px;
}

.registerInput:focus {
  outline: none;
}

.registerButton {
  margin-top: 20px;
  cursor: pointer;
  background-color: teal;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 10px;
  text-align: center;
}

.registerLoginButton {
  // position: absolute;
  width: 210px;

  margin-top: 20px;
  right: 20px;
  background-color: #041e42;
  cursor: pointer;
  padding: 10px;
  border: none;
  color: white;
  border-radius: 10px;
}
.LoginLink {
  color: white;
  text-decoration: none;
}
.spanwrong {
  color: rgb(255, 255, 255);
  font-size: 18px;
  margin-top: 10px;
}
