  article.main-part {
    background-color: #eceae8;
    .container {
      margin-top: 0;
      margin-bottom: 0;
    }
    .main-info {
      color: #041e42;
      padding: 75px 0;
      margin-bottom: 120px;
      h2 {
        font-size: 3.125rem;
        font-family: "Griffo M";
        font-weight: 400;
        margin-bottom: 50px;
        position: relative;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      p {
        font-size: 1.3rem;
        line-height: 1.5;
        &:last-of-type {
          margin-bottom: 3rem;
        }
      }
      a {
        padding: 10px 20px;
        background: #041e42;
        color: white;
        text-transform: uppercase;
        border: 2px solid transparent;
        font-size: 0.875rem;
        &:hover {
          color: #041e42;
          background: transparent;
          border: 2px solid #041e42;
        }
      }
    }
    .main-products {
      margin-bottom: 50px;
      color: #041e42;
      h4 {
        font-family: "Griffo M";
        font-size: 1.875rem;
        text-transform: capitalize;
        position: relative;
        margin-bottom: 5rem;
        &::after {
          position: absolute;
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
          background: #cba052;
          content: "";
          z-index: 1;
          width: 80px;
          height: 3px;
        }
      }
      .productItem {
        cursor: pointer;
        margin-bottom: 5rem;
        flex: 0 0 calc(100% / 3);
        @media all and (max-width: 992px) {
          flex: 0 0 50%;
        }
        @media all and (max-width: 768px) {
          flex: 0 0 100%;
        }
        &:hover img {
          transform: translate(-50%, -50%) scale(1.1);
        }
        &__imageBox {
          position: relative;
          width: 100%;
          height: 12rem;
          overflow: hidden;
          img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            min-width: 100%;
            height: auto;
            @media all and (max-width: 500px) {
              width: auto;
              height: 100%;
            }
          }
        }
        &__info {
          background-color: white;
          padding: 15px;
          h6 {
            text-align: left;
            font-family: "Griffo M";
            padding: 5px 0 40px;
            font-size: 1.75rem;
          }
          a {
            width: 100%;
            background: #041e42;
            color: white;
            text-transform: uppercase;
            font-size: 0.875rem;
            padding: 10px 0;
            letter-spacing: 1px;
            display: block;
            border: 2px solid transparent;
            &:hover {
              border: 2px solid #041e42;
              background: transparent;
              color: #041e42;
            }
          }
        }
      }
    }
  }
  article.additional-part {
    background: white;
    padding: 50px 0 100px;
    .resourceImage {
      background: #f76786;
      position: relative;
      height: 280px;
      width: 100%;
      display: flex;
      img {
        width: 100%;
        min-height: 100%;
        align-items: center;
        justify-content: center;
      }
      &:hover {
        img {
          transform: translate(10px, -10px);
        }
      }
      @media (min-width: 768px) and (max-width: 992px) {
        height: 210px;
      }
    }
    a {
      display: block;
      padding: 25px 0 15px;
      border-bottom: 1px solid #041e42;
      font-family: "Griffo M";
      text-transform: capitalize;
      font-size: 1.875rem;
      color: #041e42;
    }
  }
}
