footer {
  color: white;
  .footer-main {
    padding: 100px 50px;
    background: #041e42;
    h4 {
      text-transform: uppercase;
      font-size: 18px;
      font-weight: 700;
      @media (min-width: 992px) {
        margin-bottom: 40px;
      }
      @media (max-width: 992px) {
        margin-bottom: 1rem;
      }
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: inside circle;
        margin-bottom: 5px;
        @media (max-width: 992px) {
          list-style: none;
        }
        a {
          font-size: 14px;
          color: white;
          text-transform: capitalize;
          &:hover {
            color: #f65275;
          }
        }
      }
    }
    .info,
    .quick-links,
    .services,
    .contact {
      padding: 0 15px;
    }
    .info {
      img {
        height: 26.6px;
        width: auto;
        @media (min-width: 992px) {
          margin-bottom: 40px;
        }
        @media (max-width: 992px) {
          margin-bottom: 1rem;
        }
      }
      p:first-of-type {
        text-align: justify;
        @media (max-width: 992px) {
          text-align: center;
        }
      }
      p:last-of-type {
        margin-top: auto;
      }
    }
    .contact {
      font-size: 14px;
      .phone-numbers,
      .emails {
        span {
          color: #636363;
          cursor: pointer;
          &:hover {
            color: #f65275;
          }
        }
      }
    }
  }
  .footer-bottom {
    background: #071621;
    padding: 30px 0;
    p {
      margin-bottom: 0;
      &:last-of-type {
        margin-bottom: 1rem;
      }
    }
    ul {
      li {
        &:not(:last-of-type) {
          margin-right: 1rem;
        }
      }
    }
  }
}
