* {
  text-decoration: none;
  box-sizing: border-box;
}
a:hover {
  text-decoration: none;
}
.mr-3 {
  margin-right: 10px;
}
.mr-2 {
  margin-right: 7px;
}
/*Carousel start*/
.carousel-wrapper {
  background-color: #041e42;
  .Carousel {
    max-width: 1322px;
    height: 600px;
    position: relative;
    margin-left: auto;
    margin-right: auto;

    img {
      width: 100%;
      height: 600px;
      filter: brightness(50%);
    }
  }
}

.carousel-item-text-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
}

.carousel-button {
  text-decoration: none;
  display: inline-block;
  padding: 10px 30px;
  background-color: transparent;
  border: 3px solid #fff;
  color: #fff;
  transition: 0.7s ease;
  margin-top: 30px;
}

.carousel-button:hover {
  background-color: #fff;
  color: #333;
}

.carousel-button-dark {
  text-decoration: none;
  display: inline-block;
  padding: 10px 30px;
  background-color: #041e42;
  border: 3px solid #041e42;
  color: #fff;
  transition: 0.7s ease;
  margin-top: 30px;
}

.carousel-button-dark:hover {
  background-color: transparent;
  color: #fff;
}
.dark:hover {
  color: #041e42;
}
/*Carousel  end*/

/*Who we are start */

.title {
  font-weight: 600;

  letter-spacing: 1.5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 5px;
    width: 50px;
    background-color: #cba052;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
  }
}
.who-we-are {
  text-decoration: none;
  height: auto;
  background-image: url("./images/homepage_map.jpg");
  background-size: cover;
  padding: 150px 0;
  text-align: center;
  color: #fff;
  background-repeat: no-repeat;

  .text-wrapper {
    h1 {
      margin: 50px 0;
    }
    p {
      font-size: 25px;
    }
  }

  .statistics {
    margin-top: 100px;
    .statistics-title {
      font-size: 3rem;
    }
    .numbers {
      margin-top: 50px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      color: #f65275;
      column-gap: 50px;
      h1 {
        font-size: 4rem;
      }
    }
  }
}

/*Who we are end*/

/*Pioneers start */

.pioneers {
  text-align: center;
  padding: 50px 0;
  .pioneers-text-wrapper {
    width: 70%;
    margin: 0 auto;
    p {
      margin-top: 20px;
      letter-spacing: 1.1px;
      font-size: 20px;
    }
  }
}

/*Pioneers  end*/

/*Investors start*/

.investors {
  text-align: center;
  padding: 50px 0;

  p {
    margin-top: 20px;
    font-size: 1.3rem;
  }
}

/*Investors end*/

/*Join start*/

.join {
  width: 100%;
  height: auto;
  background-image: url("./images/img-2.jpg");
  text-align: center;
  padding: 150px 0;
  color: #fff;
  background-repeat: no-repeat;
  background-size: cover;
  .join-wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    h1 {
      margin: 50px 0;
    }
  }
}

/*Join end*/

@media (max-width: 900px) {
  .Carousel img {
    object-fit: cover;
  }
  .who-we-are .text-wrapper p {
    font-size: 1rem;
  }
  .who-we-are .statistics .numbers {
    grid-template-columns: repeat(1, 1fr);
  }
  .who-we-are .text-wrapper h1 {
    font-size: 1.5rem;
  }
  .who-we-are .statistics .numbers h1 {
    font-size: 3rem;
  }
}

@media (max-width: 600px) {
  .carousel-button {
    padding: 7px 15px;
    font-size: 0.7rem;
  }
  .carousel-button-dark {
    padding: 7px 15px;
    font-size: 0.7rem;
  }
  .break {
    display: none;
  }
  .who-we-are .container {
    padding-left: 20px !important;
    padding-right: 20px !important;
  }
  .who-we-are .text-wrapper p {
    font-size: 1rem;
  }
  .who-we-are .statistics .numbers h1 {
    font-size: 2rem;
  }
  .carousel-item-text-wrapper {
    width: 80%;
  }
  .join .join-wrapper h1 {
    width: 90%;
    margin: 50px auto;
    font-size: 1.5rem;
  }
}
@media (max-width: 324px) {
  .carousel-button {
    margin: 0 !important;
    margin-top: 10px !important;
  }
  .carousel-button-dark {
    display: block;
    margin: 0 !important;
    margin-top: 10px !important;
  }
}
