.ContactShowcase {
  height: 600px;
  color: #fff;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 600px;
    width: 100%;
    background-image: url("./images/contactusen.jpg");
    filter: brightness(60%);
    z-index: -1;
    background-size: cover;
  }
  .text-wrapper {
    animation-name: toUp;
    animation-iteration-count: 1;
    animation-duration: 1.5s;
    h1 {
      font-weight: 700;
      font-size: 3.5rem;
    }
    .showcase-btn {
      margin-top: 30px;
      padding: 10px 25px;
      border: 2px solid #fff;
      background-color: #f65275;
      transition: 0.5s;
      color: #fff;
      font-weight: 600;
      &:hover {
        background-color: transparent;
      }
      &:first-child {
        margin-right: 10px;
      }
    }
  }
  &::after {
    position: absolute;
    content: "";
    height: 20vh;
    width: 0.5px;
    background-color: #b4b4b4;
    bottom: 0;
    left: 50%;
    animation-name: toUpSign;
    animation-iteration-count: 1;
    animation-duration: 4s;

    transform: translate(-50%, 50%);
  }
}
@keyframes toUp {
  0% {
    transform: translateY(300px);
    opacity: 0.1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes toUpSign {
  0% {
    transform: translate(-50%, 100%);
    opacity: 0.1;
    background-color: transparent;
  }
  50% {
    background-color: #b4b4b4;
  }
  100% {
    transform: translate(-50%, 50%);
    opacity: 1;
  }
}

.container {
  .contact-title {
    width: 100%;
    height: 83px;
    position: relative;
    font-size: 36px;
    line-height: 42px;
    font-weight: 800;
  }
  .contact-title::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 80px;
    height: 3px;
    background-color: black;
  }
  .contact-list {
    padding-top: 40px;
    .icon {
      width: 15px;
      height: 19px;
    }
  }
}
.contact-list {
  li {
    font-size: 1.3rem;
    margin-bottom: 16px;
    font-weight: 600;
  }
  li a {
    font-size: 17px;
    color: #000;
    font-weight: 600;
    margin-left: 10px;
    li svg {
      font-size: 40px;
    }
  }
}
.list {
  span {
    font-size: 14px;
    font-weight: 900;
  }
  li {
    font-size: 15px;
  }
  li strong {
    font-weight: 800;
  }
}
.list {
  ul {
    li {
      list-style-type: disc;
    }
  }
}
.icon {
  width: 25px !important;
  height: 25px;
}
.contact-form {
  padding-top: 40px;
  .contact-1 {
    border: 6px solid #000;
    padding: 40px;
    label {
      font-weight: 600;
      margin-bottom: 10px;
    }
    .form-control {
      border: 0px;
      border-bottom: 1px solid #000;
      box-shadow: none !important;
    }
    .form-title {
      margin-bottom: 25px;
    }
  }
}
.link-icon :hover {
  background: rgb(29, 29, 29);
  transition: all 0.2s ease;
  transform: scale(1.1);
  color: #fff;
}
.batr {
  padding-top: 80px;
  button {
    width: 120px;
    height: 40px;
    background: #000;
    color: #fff;
    border: none;
    text-align: center;
    font-size: 12px;
    line-height: 18px;
  }

  .map {
    width: 100%;
    z-index: 1000;
  }
}
@media screen and (max-width: 792px) {
  .test {
    padding: 0 0 0 50px;
  }
  .contact-form {
    .contact-1 {
      padding: 15px 10px;
    }
  }
  .row {
    .contact-title {
      font-size: 30px;
      line-height: 35px;
    }
  }
}
@media screen and (max-width: 475px) {
  .link-icon {
    .iconInformation {
      margin-top: 10px !important;
    }
  }
  .row {
    .contact-title {
      font-size: 20px;
      line-height: 25px;
    }
  }
}
