section#stadium {
  header {
    position: relative;
    height: 500px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 100%;
      z-index: -1;
      width: 100%;
      object-fit: cover;
    }
    .stadium__heading {
      &__tag {
        width: 200px;
        border-top: 10px solid #fff;
        border-bottom: 10px solid #fff;
        border-left: 10px solid #fff;
        padding: 70px 0 70px 30px;
        position: relative;
        &::after,
        &::before {
          content: "";
          background: #fff;
          height: 50px;
          width: 10px;
          display: block;
          position: absolute;
          right: 0;
        }
        &::after {
          top: 0;
        }
        &:before {
          bottom: 0;
        }
        h1 {
          width: 640px;
          text-transform: uppercase;
          color: #fff;
          letter-spacing: 5px;
          font-weight: 300;
          line-height: 24px;
          font-size: 18px;
          margin: 20px 0 24px;
        }
      }
      &__breadcrumbs {
        padding: 20px 0;
        ul {
          display: flex;
          li {
            &:not(:last-of-type) {
              margin-right: 3px;
            }
            a {
              font-size: 12px;
              color: #fff;
              text-transform: capitalize;
              display: flex;
              align-items: center;
              transition: color 0.3s linear;
              &:hover {
                color: #000;
              }
            }
          }
        }
      }
    }
  }
  main {
    padding: 70px 0;
    .stadium__description {
      &__content {
        b {
          font-weight: bold;
          font-size: 1.3rem;
          margin-bottom: 20px;
          display: inline-block;
        }
        p {
          font-size: 1.3rem;
        }
      }
    }
    article.stadium__products {
      h2 {
        padding: 20px 0 24px;
        font-size: 48px;
        color: #000;
        font-weight: 600;
      }
    }
  }
}
