.Text {
  justify-content: center;
  align-items: center;
  height: auto;
  height: auto;
  text-align: center;
  padding-top: 120px;
  padding-left: 50px;
  padding-bottom: 50px;

  padding-right: 50px;

  .Text-text {
    justify-content: center;
    left: 34%;
    color: #041e42;
    text-align: center;
    position: absolute;
    align-items: center;

    .Text-h1 {
      font-size: 38px;
      font-weight: 600;
      font-family: grifo_mmedium, "Times New Roman", serif;
    }

    .Text-line {
      border-top: 4px solid rgb(182, 119, 1);
      left: 43%;
      position: absolute;
      background-color: saddlebrown;
      width: 50px;
      margin-top: 7%;
    }
    .Text-p {
      margin-top: 30%;
      font-size: 20px;
    }
  }
  @media screen and (max-width: 900px) {
    .Text-text {
      margin-left: -100px;
      margin-top: -100px;

      font-size: 14px;
      .Text-h1 {
        font-size: 30px;
      }
      .Text-p {
        margin-top: 18%;
      }
    }
    .Text-row {
      flex-direction: column;
      .news-section-2-col {
        margin-top: 5%;
      }
    }
  }
}
