/*Showcase start*/
.img-wrapper {
  position: relative;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: "";
    background-image: url("./images/image-1.jpg");
    background-size: cover;

    position: absolute;
    filter: brightness(70%);
    width: 100%;
    height: 100%;
  }
  h1 {
    user-select: none;
    font-weight: 700;
    color: #fff;
    font-size: 3rem;
    z-index: 100;
    position: absolute;
  }
  &::after {
    position: absolute;
    content: "";
    height: 30vh;
    width: 0.5px;
    background-color: #b4b4b4;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}

.text-wrapper {
  text-align: center;
  width: 60%;
  margin: 100px auto;
  .title {
    font-weight: 600;
    letter-spacing: 1.5px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      height: 5px;
      width: 50px;
      background-color: #cba052;
      bottom: -20px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  p {
    margin-top: 50px;
    font-size: 1.4rem;
  }
}
/*Showcase end*/

@media (max-width: 767px) {
  .img-wrapper {
    &::before {
      background-image: url("./images/image-2.jpg");
    }
    h1 {
      font-size: 2.8rem;
    }
  }

  .text-wrapper {
    width: 75%;
  }
}
@media (max-width: 575px) {
  .img-wrapper {
    &::before {
      background-image: url("./images/image-3.jpg");
      background-position-x: 25%;
    }
    h1 {
      font-size: 2rem;
    }
  }
  .text-wrapper {
    width: 85%;
  }
}
