section.single-product-items {
  header {
    position: relative;
    .header-image-wrapper {
      height: 500px;
      z-index: 99;
      width: 100%;
      position: relative;
      overflow: hidden;
      img {
        position: absolute;
        min-width: 100%;
        min-height: 100%;
      }
    }
    .header-content-wrapper {
      position: absolute;
      background: #041e42;
      bottom: 0;
      z-index: 999;
      left: 50%;
      transform: translateX(-50%);
      padding: 15px;
      h1 {
        color: #fff;
        font-size: 25px;
        text-transform: uppercase;
        margin: 0;
        font-weight: 700;
      }
    }
  }
  main {
    padding: 5rem 0;
    article {
      h2 {
        display: block;
        margin-bottom: 50px;
        width: 100%;
        @media all and (max-width: 576px) {
          text-align: center;
        }
      }
      .single-product-card {
        margin-bottom: 1rem;
        flex: 0 0 25%;
        padding: 0 1rem;
        margin: 0 0 2rem;
        cursor: pointer;
        @media all and (max-width: 992px) {
          flex: 0 0 calc(100% / 3);
        }
        @media all and (max-width: 768px) {
          flex: 0 0 calc(100% / 2);
        }
        @media all and (max-width: 576px) {
          flex: 0 0 100%;
        }
        &:hover .single-product-card-image-wrapper {
          img {
            transform: translate(-50%, -50%) scale(1.2);
          }
        }
        &:hover .single-product-card-content-wrapper {
          background: #fff;
          border: 3px solid #041e42;
          h3 {
            color: #041e42;
          }
        }
        .single-product-card-image-wrapper {
          position: relative;
          width: 100%;
          height: 8rem;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
        .single-product-card-content-wrapper {
          border: 3px solid transparent;
          height: 7rem;
          background: #041e42;
          padding: 1rem 3rem;
          h3 {
            color: #fff;
            font-weight: 600;
            text-align: center;
            margin-bottom: 40px;
            font-size: 18px;
            text-transform: capitalize;
          }
        }
      }
    }
  }
}
